body {
  font-size: 18px;
  color: #333;
  font-family: "Comic Neue", sans-serif;
}

h1 {
  margin-top: 50px;
  text-align: center;
}

.app {
  max-width: 350px;
  margin: 0 auto;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #333;
  box-shadow: 2px 2px 10px rgba(55, 55, 55, 0.5);
}

form {
  width: 100%;
}

input[type="search"] {
  width: 100%;
  padding: 5px;
  border-radius: 2px;
  border: 1px solid #333;
  font-family: inherit;
  font-size: inherit;
}

input[type="search"]:focus {
  border-color: blue;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

li {
  display: flex;
  align-items: flex-start;
  margin-top: 10px;
  padding: 5px;
  border: 1px solid #333;
  border-radius: 2px;
}

li.completed {
  text-decoration: line-through;
}

input[type="checkbox"] {
  margin-right: 10px;
}

button {
  position: relative;
  margin-left: auto;
  padding: 8px;
  border: none;
  background-color: transparent;
  font-size: 0;
  opacity: 0.7;
  align-self: center;
}

button:hover {
  cursor: pointer;
  opacity: 1;
}

button::before,
button::after {
  content: "";
  position: absolute;
  left: 0;
  width: 10px;
  height: 1px;
  background-color: red;
}

button::before {
  transform: rotate(45deg);
}

button::after {
  transform: rotate(-45deg);
}
